import React, { Component } from 'react'
import { connect } from 'react-redux'
import { GoogleOauth } from './GoogleOauth'
// import '../../node_modules/bootstrap/dist/css/bootstrap.min.css';

import {
  loadUsers,
  removeUser,
  login,
  logout,
  signup,
  guestLogin
} from '../store/actions/userActions'

class _Login extends Component {
  state = {
    msg: '',
    loginCred: {
      email: '',
      password: '',
    },
    signupCred: {
      email: '',
      password: '',
      username: '',
    },
    section: true,
  }

  loginHandleChange = (ev) => {
    const { name, value } = ev.target
    this.setState((prevState) => ({
      loginCred: {
        ...prevState.loginCred,
        [name]: value,
      },
    }))
  }

  signupHandleChange = (ev) => {
    const { name, value } = ev.target
    this.setState((prevState) => ({
      signupCred: {
        ...prevState.signupCred,
        [name]: value,
      },
    }))
  }

  doLogin = async (ev) => {
    ev.preventDefault()
    const { email, password } = this.state.loginCred
    if (!email || !password) {
      return this.setState({ msg: 'Please enter user/password' })
    }
    const userCreds = { email, password }
    const user = this.props.login(userCreds)
    user.then(data=>
      {if(data){
        this.setState({ loginCred: { email: '', password: '' } })
        this.props.closeModal()
        window.location.assign('/#/boards/')
      }else{
        return this.setState({ msg: 'Invalid email or password'})
      }
    })
  }

  doSignup = async (ev) => {
    ev.preventDefault()
    const { email, password, username } = this.state.signupCred
    if (!email || !password || !username) {
      return this.setState({ msg: 'All inputs are required!' })
    }
    const signupCreds = { email, password, username }
    this.props.signup(signupCreds)
    this.setState({ signupCred: { email: '', password: '', username: '' } })
    this.props.closeModal()
    window.location.assign('/#/boards/')
  }

  removeUser = (userId) => {
    this.props.removeUser(userId)
  }

  onSignup = () => {
    this.setState({ section: !this.state.section })
  }

  render() {
    let signupSection = (
      <form onSubmit={this.doSignup} className='signup-container'>
        <h3>Register</h3>
        <div className="form-group">
          <label>User name</label>
          <input 
          type="text" 
          className="form-control" 
          placeholder="User name" 
          name='username'
          value={this.state.signupCred.username}
          onChange={this.signupHandleChange}
          maxLength="10"
          />
        </div>

        {/* <div className="form-group">
          <label>First name</label>
          <input 
          type="text" 
          className="form-control" 
          placeholder="First name" 
          name='username'
          value={this.state.signupCred.username}
          onChange={this.signupHandleChange}
          maxLength="10"
          />
        </div>
        <div className="form-group">
          <label>Last name</label>
          <input 
          type="text" 
          className="form-control" 
          placeholder="Last name" 
          name='username'
          value={this.state.signupCred.username}
          onChange={this.signupHandleChange}
          maxLength="10"
          />
        </div> */}

        <div className="form-group">
          <label>Email</label>
          <input 
          type="email" 
          className="form-control" 
          placeholder="Enter email"
          name='email'
          value={this.state.signupCred.email}
          onChange={this.signupHandleChange} 
          />
        </div>

        <div className="form-group">
          <label>Password</label>
          <input 
          type="password" 
          className="form-control" 
          placeholder="Enter password" 
          name='password'
          value={this.state.signupCred.password}
          onChange={this.signupHandleChange}
          />
        </div>

        <button type="submit" className="btn btn-yellow btn-lg btn-block">Register</button>
        <p className="text-right">
            Already registered <span className="small-text" onClick={this.onSignup}>log in?</span>
        </p>
      </form>
    )
    let loginSection = (
      <form onSubmit={this.doLogin} className='login-container'>
        <h3>Log in</h3>
        <div className="form-group">
          <label>Email</label>
          <input
          type="email" 
          className="form-control" 
          placeholder="Enter email" 
          name='email'
          value={this.state.loginCred.email}
          onChange={this.loginHandleChange}
          />
        </div>
        <div className="form-group">
          <label>Password</label>
          <input 
          type="password" 
          className="form-control" 
          placeholder="Enter password" 
          name='password'
          value={this.state.loginCred.password}
          onChange={this.loginHandleChange}
          />
        </div>

        <button type="submit" className="btn btn-yellow btn-lg btn-block">Sign in</button>
        <button onClick={this.onSignup} className="btn btn-yellow btn-lg btn-block">Sign up</button>

      </form>
    )

    const { loggedInUser } = this.props
    return (
      <div className='login-section'>

        <div className="outer">

          <div className="google-oauth">
            <GoogleOauth className="google-oauth-btn" closeModal={this.props.closeModal}/>
          </div>

          <div className="inner">
              <h2 className='msg'>{this.state.msg}</h2>
              {!loggedInUser && this.state.section ? loginSection : signupSection}
          </div>
          
        </div>

      </div>
    )
  }
}

const mapStateToProps = (state) => {
  return {
    users: state.user.users,
    loggedInUser: state.user.loggedInUser,
    isLoading: state.system.isLoading,
  }
}
const mapDispatchToProps = {
  login,
  logout,
  signup,
  removeUser,
  loadUsers,
  guestLogin
}

export const Login = connect(mapStateToProps, mapDispatchToProps)(_Login)
